<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"/>
    </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"/>
</div>
</template>

<script> 
import { Ping } from "@/request/user.js";
export default {
  name: 'app',

  data() {
    return {
      webSocket: null,
      logArr: [],
      timer1: "",
      isConnect: true,
    };
  },
  created() {
    
    this.initWebSocket();
  },
  mounted() {
    const that = this;
    this.timer1 = setInterval(function () {
      if (that.isConnect) {
        that.isConnect = false;
        Ping().then(() => {
          that.$store.commit('changeSqlConnect', true);
          that.isConnect = true;
          })
          .catch(() => {
            that.$store.commit('changeSqlConnect', false);
            that.isConnect = true;
        });
      }
    }, 1000);    
  },
  destroyed() {
    this.webSocket.close();
  },
  beforeDestroy(){
    clearInterval(this.timer1);
  },
  close() {
    this.webSocket.close();
  },
  methods: {
    initWebSocket() {
      //初始化weosocket
      let localhost = localStorage.getItem('websocketIp');
      let port = localStorage.getItem("websocketPort");
      //console.log(localhost, port);
      this.webSocket = new WebSocket("ws://" + localhost + ":" + port + "");
      this.$store.commit('changeWebSocket', this.webSocket);
      this.webSocket.onopen = this.onOpen;
      this.webSocket.onmessage = this.onMessage;
      this.webSocket.onerror = this.onError;
      this.webSocket.onclose = this.onClose;
    },
    onMessage(event) {
      if (event.data[0] == '{') {
        //localStorage.setItem('setting', event.data);
        //console.log(event.data);
        this.$store.commit('changeSetting', JSON.parse(event.data));
        return;
      }  
      var log = this.cutString(event);
      if (!this.logArr.includes(log.file)) {
        this.logArr.push(log.file);
        this.$store.commit('changeLog', {
          title: log.file,
          content: [],
        });
        this.$store.commit('changeFile', {
          title: log.file,
          total: 0,
        });
      }
      var logIndex = this.logArr.indexOf(log.file);
      this.$store.commit('changeTotal');
      this.$store.commit('changeLogContent', {logIndex: logIndex, log: log});
      this.$store.commit('changeFileTotal', logIndex);
      this.$store.commit('changeMonthTotal', new Date(log.date).getMonth());
      switch(log.level) {
        case "debug":
          this.$store.commit('changeLevelTotal', 0);
          break;
        case "info":
          this.$store.commit('changeLevelTotal', 1);
          break;
        case "warning":
          this.$store.commit('changeLevelTotal', 2);
          break;
        case "error":
          this.$store.commit('changeLevelTotal', 3);
          break;
      }
    },
    cutString(event) {
      var str = new String();
      var arr = new Array();
      str = event.data;
      arr = str.split(" ");
      let len =
        arr[0].length + arr[1].length + arr[2].length + arr[3].length + 4;
      return {
        date: arr[0].slice(1, arr[0].length),
        time: arr[1].slice(0, arr[1].length - 1),
        file: arr[2].slice(1, arr[2].length - 1),
        level: arr[3].slice(1, arr[3].length - 1),
        message: str.slice(len),
      };
    },
    onOpen() {
      //console.log("open");

    },
    onError() {
      //console.log("error");
      this.initWebSocket();
    },
    onClose() {
      //console.log("close");
    },
  },
}
</script>